.slide_up_text,
.slide_up_text>div,
.slide_up_text>li {
  overflow: hidden;
}
.slide_up_text span,
.slide_up_text>div span,
.slide_up_text>li span,
.slide_up_text a,
.slide_up_text>div a,
.slide_up_text>li a {
  display: block;
  transition: $slide_up_text;
  transform: translate(0, 100%);
}
.slide_up_text.slide_up_text--active span,
.slide_up_text>div.slide_up_text--active span,
.slide_up_text>li.slide_up_text--active span,
.slide_up_text.slide_up_text--active a,
.slide_up_text>div.slide_up_text--active a,
.slide_up_text>li.slide_up_text--active a {
  display: block;
  transform: translate(0, 0%);
}
.slide_up_text.slide_up_text--active div:nth-child(2) span,
.slide_up_text>div.slide_up_text--active div:nth-child(2) span,
.slide_up_text>li.slide_up_text--active div:nth-child(2) span {
  transition-delay: 0.3s;
}
.slide_up_text.slide_up_text--active div:nth-child(3) span,
.slide_up_text>div.slide_up_text--active div:nth-child(3) span,
.slide_up_text>li.slide_up_text--active div:nth-child(3) span {
  transition-delay: 0.6s;
}
